.project {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.project .cols {
  display: flex;
  background: white;
}

.project .left-col {
  width: 60%;
  background: white;
}

.project .right-col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 40%;
  background: white;
}

.project img {
  width: 100%;
}

.right-col > * {
  margin-bottom: 10px;
}

.view__icon {
  margin-right: 10px;
  font-size: 24px;
}

.view-live,
.view-code {
  display: flex;
  align-items: center;
}
