.project__home {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  background-size: 100%;
  background-repeat: no-repeat;
}

.project__cols {
  display: flex;
  justify-content: space-around;
}

.project__cols > div {
  background-color: #eeeeee;
  margin: 0 20px;
  transition: transform 450ms;
  position: relative;
  padding: 0px 20px 50px 20px;
}

.project__cols > div:hover {
  transform: scale(1.05);
}

.col__title {
  text-align: center;
}

.col__buttons {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
}

.col__buttons > button {
  margin-right: 10px;
}
