.homepage{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100vh;
    background-size: 100%;
    background-repeat: no-repeat;
}

.intro-text{
    display:flex;
    margin-top: -100px;
    width:50%;
    min-width:100px;
    padding: 0 30px;
}

.intro-text h2{ 
    font-family: Roboto Mono;
    font-style: normal;
    font-weight: normal;
    font-size: 1.5em;
    line-height: 1em;
    letter-spacing: 0.05em;
    position:relative;
}

.intro-text h1{
    font-family: Roboto Mono;
    font-style: normal;
    font-weight: normal;
    font-size: 50px;
    line-height: 1em;
    display: flex;
    letter-spacing: 0.05em;
    position: relative;
}

.intro-text p{
    font-family: Roboto Mono;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    line-height: 1em;
    display: flex;
    letter-spacing: 0.05em;
    top: 200px;
    position: relative;
}

.big-name{
    display:flex;
    flex-direction: column;
    width:50%;
    align-items: flex-end;
    position: relative;
    top: 12px;
    right: 100px;
}

.big-name h1{
    font-family: Roboto Mono;
    font-style: normal;
    font-weight: bold;
    font-size: 10em;
    line-height: 1em;
    display: flex;
    align-items: center;
    letter-spacing: 0.05em;
    color: #FFFFFF;
    text-transform: uppercase;
    margin-top:0;
}

.big-name p{
    position:relative;
    top: 80px;
    font-family: Roboto Mono;
    font-style: normal;
    font-weight: bold;
    font-size: .9em;
    line-height: 1em;
    display: flex;
    align-items: center;
    letter-spacing: 0.1em;
    justify-content: flex-end;
    color: #FFFFFF;
}